<template>
  <div class="frame-container" style="background: #eee;">
    <div class="vm-padding-height page-home-head">
      <div class="vm-postion-box">
        <p class="lansebg"></p>
        <div class="dataOverview" style=" margin-top: -40px;">
          <p class="label">
            <span class="border_lan">数据总览</span>
            <span>
              <el-tooltip placement="top">
                <div slot="content" class="text">
                  <p>销售金额=交易金额-退款金额</p>
                  <p>交易金额：所有商户的交易流水总额</p>
                  <p>销售金额：所有商户的实际营收总额</p>
                  <p>佣金金额：所有商户有效交易中抽取的佣金总额</p>
                </div>
                <i class="el-icon-question"></i>
              </el-tooltip>
            </span>
          </p>
          <div class="flex data" style="border-bottom: 1px dashed #E3E3E3;">
            <div class="content">
              <p>
                销售总额
                <el-tooltip placement="top">
                  <div slot="content" class="text">
                    <p>交易笔数：{{dataList.transactionCount}}</p>
                    <p>交易金额:{{dataList.transactionMoney}}</p>
                    <p>退款笔数：{{dataList.refundCount }}</p>
                    <p>退款金额：{{dataList.refundMoney }}</p>
                  </div>
                  <i class="el-icon-arrow-down"></i>
                </el-tooltip>
              </p>
              <p>
                ￥
                <span class="lanse">{{dataList.transactionMoney}}</span>
              </p>
            </div>
            <div class="content">
              <p>佣金总额</p>
              <p>
                ￥
                <span class="heise">{{dataList.commissionMoney}}</span>
              </p>
            </div>
            <div class="content" @click="toCommissionRecord">
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
          <div class="flex data">
            <!-- 三级代理没有下级代理，显示业务员数量 -->
            <div class="content" v-if="dataList.type!=4">
              <p>
                <span>代理总数</span>
                <el-tooltip placement="bottom">
                  <div slot="content" class="text">
                    <p>一级代理：{{dataList.firstAgentNum }}</p>
                    <p>二级代理：{{dataList.secondAgentNum }}</p>
                    <p>三级代理：{{dataList.thirdAgentNum }}</p>
                  </div>
                  <i class="el-icon-arrow-down"></i>
                </el-tooltip>
              </p>
              <p>
                <span class="heise">{{dataList.countAgentNum}}</span>
              </p>
            </div>
            <div v-else>
              <p>
                <span>业务员数</span>
              </p>
              <p>
                <span class="heise">{{dataList.salesmanNum}}</span>
              </p>
            </div>
            <div class="content">
              <p>商户总数</p>
              <p>
                <span class="heise">{{dataList.merchantNum}}</span>
              </p>
            </div>
            <div class="content" @click="to_myMerchant">
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
        </div>
        <div class="dataOverview" style="margin-top:10px">
          <div class="label flex">
            <span class="border_lan">交易数据</span>
            <p>
              <span
                style="color:#8A8A8A;font-size:12px"
              >近30天({{startTime.split(" ")[0]}}至{{endTime.split(" ")[0]}})</span>
              <span style="color:#2A8FC6;font-size:12px" @click="to_tradeData">
                更多
                <i class="el-icon-arrow-right more"></i>
              </span>
            </p>
          </div>
          <div class="flex" style="padding:10px 20px">
            <div class="content">
              <p>交易笔数</p>
              <p>
                <span class="heise">{{dealList.transactionCount}}</span>
              </p>
            </div>
            <div class="content">
              <p>交易总额</p>
              <p>
                ￥
                <span class="lanse">{{dealList.transactionMoney}}</span>
              </p>
            </div>
            <div class="content">
              <p>佣金总额</p>
              <p>
                ￥
                <span class="heise">{{dealList.commissionMoney}}</span>
              </p>
            </div>
          </div>
          <div class="flex" style="padding:10px 20px;border-bottom: 1px dashed #E3E3E3;">
            <div class="content">
              <p>退款笔数</p>
              <p>
                <span class="heise">{{dealList.refundCount}}</span>
              </p>
            </div>
            <div class="content">
              <p>退款金额</p>
              <p>
                ￥
                <span class="heise">{{dealList.refundMoney}}</span>
              </p>
            </div>
            <div class="content">
              <p>营收总额</p>
              <p>
                ￥
                <span class="lanse">{{dealList.actualMoney}}</span>
              </p>
            </div>
          </div>
          <!-- 数据趋势图 -->
          <div
            class="home-container-echart"
            style="border-bottom: 1px dashed #E3E3E3;position: relative;"
          >
            <div class="echart-title">
              <div style="border-left: 1px solid #424242;text-align:left;padding-left:5px">
                <p>数据趋势图</p>
              </div>

              <div class="home-echat-btn">
                <div class="vm-flex">
                  <div @click="changeStatus(1)" :class="{active: isActive==1}">销售总额</div>
                  <div @click="changeStatus(2)" :class="{active: isActive==2}">我的佣金</div>
                </div>
              </div>
            </div>
            <div id="myChartLine" :style="{width: '100%', height: '300px'}"></div>
            <p v-if="noData && isActive==1" style="color:#ccc;font-size:10px;padding-bottom:20px">
              <i class="el-icon-warning"></i>暂无数据
            </p>
          </div>
          <!-- 支付通道占比 -->
          <div class="home-container-echart" style="border-bottom: 1px dashed #E3E3E3;">
            <div class="echart-title">
              <div style="border-left: 1px solid #424242;text-align:left;padding-left:5px">
                <p>支付通道占比</p>
              </div>
              <div class="home-echat-btn">
                <div class="vm-flex">
                  <div @click="changeStatus2(1)" :class="{active: type==1}">销售总额</div>
                  <div @click="changeStatus2(2)" :class="{active: type==2}">交易笔数</div>
                </div>
              </div>
            </div>
            <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
          </div>
          <div class="home-container-echart" style="height:100%;padding-bottom:60px">
            <div class="echart-title">
              <div style="border-left: 1px solid #424242;text-align:left;padding-left:5px">
                <p>商家营收排行榜</p>
              </div>
            </div>
            <!--  -->
            <ul
              class="rankingUrl"
              style="display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 3;overflow: hidden;"
            >
              <li v-for="(item,index) in rankList" :key="index">
                <p class="rank_label">{{item.storeName }}</p>
                <div class="flex">
                  <p>
                    <span v-if="item.companyType===1">#我的</span>
                    <span v-if="item.companyType===2">#一级代理</span>
                    <span v-if="item.companyType===3">#二级代理</span>
                    <span v-if="item.companyType===4">#三级代理</span>
                    <span>业务员：{{item.companyName }}</span>
                  </p>
                  <p class="money">￥{{item.countPayPrice }}</p>
                </div>
              </li>
            </ul>
            <p v-if="!rankList" style="margin:30px 0;color:#ccc;font-size:10px">
              <i class="el-icon-warning"></i>暂无数据
            </p>
            <p v-else @click="cheackAll" style="color:#2A8FC6;margin-top:10px">查看全部</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { homeApi } from "@/api";
import * as types from "@/router/types";
import { afterLoginInfoLocal } from "@/storage";

export default {
  data() {
    return {
      isActive: 1,
      companyId: "",
      userType: "",
      levelAlias: {},
      startTime: "",
      endTime: "",
      dataList: "",
      dealList: "",
      type: 1,
      mapList:{},
      payList: [],
      rankList: [],
      payPriceType: 0,
      noData: false,
      noData2: false
    };
  },
  created() {
    this.startTime = this.getDay(-30);
    this.endTime = this.getDay(0);
    let userInfo = afterLoginInfoLocal.getJSON();
    this.levelAlias = userInfo.levelAlias;
    this.companyId = userInfo.companyId;
    this.userType = userInfo.userType;

    this.getcount_data();
  },
  methods: {
    // 不渲染
    getcount_data() {
      homeApi
        .count_data(this.companyId, this.startTime, this.endTime, this.type)
        .then(
          res => {
            this.getData();
            this.getdeal();
            this.getdataMap();
            this.getpayProportion();
            this.getranking();
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    to_myMerchant() {
      this.$router.push({
        name: types.MYMERCHANT,
        query: { hometype: this.dataList.type }
      });
    },
    cheackAll() {
      this.$router.push({ name: types.SALEROOM });
    },
    toCommissionRecord() {
      this.$router.push({ name: types.COMMISSIONRECORD });
    },
    to_tradeData() {
      this.$router.push({ name: types.TRADEDATA });
    },
    //获取排行榜
    getranking() {
      homeApi
        .ranking(
          this.companyId,
          this.startTime,
          this.endTime,
          this.payPriceType
        )
        .then(
          res => {
            this.rankList = res.obj;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    // 支付通道占比
    getpayProportion() {
      homeApi
        .payProportion(this.companyId, this.startTime, this.endTime, this.type)
        .then(
          res => {
            let param = res.obj;
            console.log(param, "param");
            if (!param) {
              this.noData2 = true;
            }
            this.payList = [{value:0,name:""}];
            param.forEach(item => {
              this.payList.push({ value: item.payMoney, name: item.payName });
            });

            this.drawLine2();
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    // 数据趋势图(销售总额)
    getdataMap() {
      homeApi
        .dataMap(this.companyId, this.startTime, this.endTime, this.type)
        .then(
          res => {

            this.mapList = res.obj;

            if (!res.obj) {
              this.noData = true;
              this.mapList={num:["0"],xdata:[""],ydata:["0"]}
              console.log(this.mapList,"mapList")
              this.drawLine();
            } else {
              this.drawLine();
            }
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    // 数据趋势图(我的佣金)
    getdataMap2() {
      homeApi
        .dataMap2(this.companyId, this.startTime, this.endTime, this.type)
        .then(
          res => {
            this.mapList = res.obj;
            if (this.isActive === 2) {
              this.drawLine();
            }
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    changeStatus(index) {
      this.isActive = index;
      if (this.isActive === 1) {
        this.getdataMap();
      } else {
        this.getdataMap2();
      }
    },
    changeStatus2(index) {
      this.type = index;
      this.getpayProportion();
    },
    // 获取日期
    getDay(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = today.getHours();
      var tMinutes = today.getMinutes();
      var tSeconds = today.getSeconds();
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    doHandleMonth(month) {
      var m = month;
      if (month.toString().length == 1) {
        m = "0" + month;
      }
      return m;
    },
    // 交易数据
    getdeal() {
      homeApi
        .dataTransaction(this.companyId, this.startTime, this.endTime)
        .then(
          res => {
            this.dealList = res.obj;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    // 数据总览
    getData() {
      homeApi.dataView(this.companyId).then(
        res => {
          this.dataList = res.obj;
          console.log(this.dataList, "dataList");
        },
        err => {
          this.$toast.error(err.msg);
        }
      );
    },
    // 数据趋势图
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChartLine"));
      // 绘制图表
      myChart.setOption({
        xAxis: {
          type: "category",
          data: this.mapList.xdata
        },
        yAxis: {
          type: "value",
          show: true,
          data: this.mapList.ydata
        },
        series: [
          {
            data: this.mapList.num,
            type: "line"
          }
        ],
        color: "#4ba8ff"
      });
    },
    // 支付通道占比
    drawLine2() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 绘制图表
      myChart.setOption({
        tooltip: {
          //提示框，可以在全局也可以在
          trigger: "item", //提示框的样式
          formatter: "{a} <br/>{b}: {c} ({d}%)",
          color: "#000", //提示框的背景色
          textStyle: {
            //提示的字体样式
            color: "black"
          }
        },
        series: [
          {
            name: "访问来源",
            type: "pie", //环形图的type和饼图相同
            radius: ["30%", "40%"], //饼图的半径，第一个为内半径，第二个为外半径
            avoidLabelOverlap: false,
            color: ["#D1FBEF", "#F9D858", "#4CD0DD", "#DF86F0", "#F5A7C1"],
            label: {
              normal: {
                //正常的样式
                show: true,
                position: "left"
              },
              emphasis: {
                //选中时候的样式
                show: true,
                textStyle: {
                  fontSize: "20",
                  fontWeight: "bold"
                }
              }
            }, //提示文字
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this.payList
          }
        ]
      });
    }
  }
};
</script>



